<template>
    <h2 class="text-center my-5">{{ $t('Contact') }}</h2>
      <!--Contacts-->
  <section ref="contact">
  <MDBContainer class="my-5">
  <MDBRow>
    <MDBCol md="6">             
          <form role="form" method="POST" @submit.prevent="sendEmail">
      <!-- Name input -->
      <MDBInput
        type="text"
        label="Name"
        id="form4Name"
        v-model="nameMsg"
        wrapperClass="mb-4"
      />
  
      <!-- Email input -->
      <MDBInput
        type="email"
        label="Email address"
        id="form4Email"
        v-model="emailMsg"
        wrapperClass="mb-4"
      />
  
      <!-- Message input -->
      <MDBTextarea
        label="Message"
        id="form4Textarea"
        v-model="messageMsg"
        wrapperClass="mb-4"
      />
  
      <!-- Checkbox -->
      <div class="form-check d-flex justify-content-center mb-4">
        <MDBCheckbox
          label="Send me a copy of this message"
          id="form4CopyCheck"
          v-model="form4CopyCheck"
        />
      </div>
  
      <!-- Submit button -->
      <MDBInput color="primary" block class="mb-4" type="submit" :disabled="!nameMsg || !emailMsg || !messageMsg"></MDBInput>
    </form>
  </MDBCol>
  
  <MDBCol md="6">
    <div class="text-center">
      <div class="text-center my-2" >
          <img
          src="../assets/rrLogo.png"
          height="350"
          alt=""
          loading="lazy"
        />
        </div>
    </div>
  
  </MDBCol>
  </MDBRow>
  </MDBContainer>
  </section>
  </template>
  
  
  
  <script>
   import {
      MDBInput,
      MDBCheckbox,
      
      MDBTextarea,
      MDBCol,
      MDBRow,
      MDBContainer,
    } from "mdb-vue-ui-kit";
    
    import axios from 'axios';
  
    
  
    export default {
      components: {
        MDBInput,
        MDBCheckbox,
        
        MDBTextarea,
        MDBCol,
        MDBRow,
        MDBContainer,
      },
  data() {    
  return {      
        nameMsg: '',      
        emailMsg: '',      
        messageMsg: '',      
        loadingTxt: false,
        box:false,
        };  
  },
  methods: {    
  sendEmail() {      
  this.loadingTxt= true;  
  this.box = window.alert("Mesagem enviada com sucesso!") ;    
  axios.post('https://formspree.io/f/meqbjyey',
  {
     name: this.nameMsg,          
     from: this.emailMsg,          
     _subject: `${this.nameMsg} | Friendly Message from Github Page`,
     message: this.messageMsg,},
     
  )
  .then((response) => {
    console.log(response)
    this.loadingTxt= false;
    this.nameMsg = '';
    this.emailMsg = '';
    this.messageMsg = '';
    
  })
  },
  },
  };
  
  </script>